<td width="88" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="50" height="51" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber416.jpg"></p>
</td>
<td width="145" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">湮灭</span>
<br><span class="rvts21">（</span><span class="rvts20">Annihilate</span><span class="rvts21">）</span></p>
</td>
<td width="644" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">该法杖是费恩作为主角时，投靠无名岛黑环瘦黄男人，并在瘦黄男人那里得到的法杖。</span>
<br><span class="rvts21">在杀死队友后，可以使用该法杖的湮灭技能在队友尸体上，永久杀掉并解散该队友。</span></p>
</td>
</tr>
<tr valign="top">
<td width="88" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="61" height="63" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber417.jpg"></p>
