</td>
<td width="88" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="50" height="51" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber418.jpg"></p>
</td>
<td width="145" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">破誓</span>
<br><span class="rvts21">（</span><span class="rvts20">SwornBreak</span><span class="rvts21">）</span></p>
</td>
<td width="644" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">该物品一共有两把，第一把为合成物（三个部件分别来自黑坑洞远古神庙中永恒者石棺旁的石箱，</span>
<br><span class="rvts21">瘦黄男人隐藏居所的宝箱，神学院竞技场大</span><span class="rvts20">BOSS</span><span class="rvts21">身上）；</span>
<br><span class="rvts21">第二把在林德</span><span class="rvts20"></span><span class="rvts21">凯姆藏宝室中囚禁阿虎大人处的石像处；</span>
<br><span class="rvts21">该物品可用于解放温蒂格，猩红公主，以及宣誓神王后的费恩，但物品技能只能使用</span><span class="rvts20">1</span><span class="rvts21">次后报废。</span></p>
