</tr>
<tr valign="top">
<td width="88" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="62" height="60" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber419.jpg"></p>
</td>
<td width="172" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">真视头罩</span>
<br><span class="rvts42">（</span><span class="rvts68">Cowl of True Sight</span><span class="rvts42">）</span></p>
</td>
<td width="88" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="48" height="48" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber420.jpg"></p>
</td>
<td width="145" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">清除幻像</span>
<br><span class="rvts21">（</span><span class="rvts20">Dispel Illusion</span><span class="rvts21">）</span></p>
</td>
<td width="644" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">无名岛亚历山大会给予你这个头巾用来杀掉瘦黄男人，</span>
<br><span class="rvts21">或者你杀掉亚历山大后，从他身上捡到这个头巾，</span>
<br><span class="rvts21">该头巾可用于清除墙壁上的视觉障碍物，显现出隐藏道路。</span></p>
</td>
</tr>
<tr valign="top">
<td width="88" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="60" height="61" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber421.jpg"></p>
</td>
<td width="172" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">源力吊坠</span>
<br><span class="rvts42">（</span><span class="rvts68">Source Amulet</span><span class="rvts42">）</span></p>
</td>
<td width="88" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="48" height="49" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber422.jpg"></p>
</td>
<td width="145" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">补充源力</span>
<br><span class="rvts21">（</span><span class="rvts20">Replenish Source</span><span class="rvts21">）</span></p>
</td>
<td width="644" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">在阿克斯玩具商人那里获得，他会交给你这个吊坠用于通行鲜血之路，</span>
<br><span class="rvts21">或者你杀掉玩具商人，他也会掉落该吊坠，</span>
<br><span class="rvts21">该吊坠是任务物品，可以使角色额外储存</span><span class="rvts20">5</span><span class="rvts21">点源力值。</span></p>
</td>
</tr>
